import React from "react";
import { FormattedMessage } from "react-intl";

const CardService = ({ icon, description, title }) => {
	return (
		<div className="cards moveUp fade_up">
			<div className="imgBx">
				<img src={icon} alt="" />
			</div>
			<div className="contentBx">
				<h3>
					<FormattedMessage id={title} defaultMessage={title} />
				</h3>
				<p className="price">
					<FormattedMessage
						id={description}
						defaultMessage={description}
					/>
				</p>
				<a href="#contact" className="buy custom-btn btn">
					<FormattedMessage
						id="service.btn"
						defaultMessage="Text me"
					/>
				</a>
			</div>
		</div>
	);
};

export default CardService;