import React from "react";
import "./service.css";
import CardService from "./CardService.js";
import { FormattedMessage } from "react-intl";
import { data } from "../../constants";
import { images } from "../../constants";

const Service = () => {
	return (
		<>
			<section className="waveSection">
				<img src={images.waveUp} alt="" />
			</section>
			<section id="service">
				<div className="moveUp fade_up">
					<h2><FormattedMessage
						id="title.service"
						defaultMessage="SERVICES"
					/>
					</h2>
				</div>
				<div className="serviceCard moveUp fade_up">
					{data.servicesCard.map(
						({ key, icon, title, description }) => (
							<CardService key={key} icon={icon} title={title} description={description} />
						)
					)}
				</div>
			</section>
			<section className="waveSection">
				<img src={images.waveDown} alt="" />
			</section>
		</>
	);
};

export default Service;