import React, { useEffect, useState } from "react";
import "./global.css";
import Header from "./components/header/header";
import Showcase from "./components/showcase/showcase";
import Skill from "./components/skill/skill";
import Service from "./components/services/Service";
import Project from "./components/project/project";
import Social from "./components/social/social";
import Footer from "./components/footer/footer";

function App() {
	const [scrollFade, setScrollFade] = useState(0);

	useEffect(() => {
		
			window.addEventListener("scroll", function () {
				function fade(direction) {
					let object = document.querySelectorAll(
						".fade_" + direction
					);

					for (let i = 0; i < object.length; i++) {
						let heightWindow = window.innerHeight / 1.3;

						let distance = object[i].getBoundingClientRect().top;

						object[i].classList.add("transform_" + direction);

						if (distance <= heightWindow) {
							object[i].classList.add("showUp");
						} else {
							object[i].classList.remove("showUp");
						}
					}
				}

				fade("up");
			});
	
	}, [scrollFade]);

	return (
		<>
			<div className="container">
				<Header />
				<hr />
				<Showcase />
				<Skill />
				<Service />
				<Project />
				<Social />
			</div>
			<Footer />
		</>
	);
}

export default App;